"use client"

import { useEffect, useMemo, useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { cn } from "@/lib/utils"
import axiosInstance from "@/../ultis/axios"

interface Task {
  id: string
  name: string
  start: number
  duration: number
  progress: number
  status: "completed" | "in-progress" | "pending"
  assignee: string
}

type GanttChartProps = { projectId: string }

// Convert backend milestone to Task for display
function mapMilestoneToTask(m: any, projectStart: Date): Task {
  const startDate = new Date(m.start_date)
  const endDate = new Date(m.deadline)
  const weekMs = 7 * 24 * 60 * 60 * 1000
  const startWeeks = Math.max(0, Math.floor((startDate.getTime() - projectStart.getTime()) / weekMs))
  const durationWeeks = Math.max(1, Math.ceil((endDate.getTime() - startDate.getTime()) / weekMs))
  const normalizedStatus = m.status === "done" ? "completed" : m.status === "in_progress" ? "in-progress" : "pending"
  return {
    id: m._id,
    name: m.title ?? "Untitled",
    start: startWeeks,
    duration: durationWeeks,
    progress: typeof m.progress === "number" ? m.progress : normalizedStatus === "completed" ? 100 : 0,
    status: normalizedStatus,
    assignee: m.owner_name ?? "",
  }
}

export default function GanttChart({ projectId }: GanttChartProps) {
  const [milestones, setMilestones] = useState<any[]>([])
  const [loading, setLoading] = useState<boolean>(false)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    let isMounted = true
    async function load() {
      try {
        setLoading(true)
        setError(null)
        const res = await axiosInstance.get(`/api/projects/${projectId}/milestones`)
        if (!isMounted) return
        setMilestones(Array.isArray(res.data) ? res.data : [])
      } catch (e: any) {
        if (!isMounted) return
        setError(e?.response?.data?.message ?? e?.message ?? "Failed to load milestones")
      } finally {
        if (isMounted) setLoading(false)
      }
    }
    if (projectId) load()
    return () => {
      isMounted = false
    }
  }, [projectId])

  const projectStart = useMemo(() => {
    const dates = milestones
      .map(m => new Date(m.start_date))
      .filter(d => !isNaN(d.getTime()))
    return dates.length ? new Date(Math.min(...dates.map(d => d.getTime()))) : new Date()
  }, [milestones])

  const tasks: Task[] = useMemo(() => {
    return milestones.map(m => mapMilestoneToTask(m, projectStart))
  }, [milestones, projectStart])

  const totalWeeks = Math.max(10, Math.min(52, tasks.reduce((max, t) => Math.max(max, t.start + t.duration), 0)))
  const weeks = useMemo(() => Array.from({ length: totalWeeks }, (_, i) => `Week ${i + 1}`), [totalWeeks])

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle className="font-sans text-2xl">Gantt Chart</CardTitle>
        <CardDescription>Project timeline and task dependencies</CardDescription>
      </CardHeader>
      <CardContent>
        <div className="overflow-x-auto">
          <div className="min-w-[800px]">
            {loading && <div className="p-4 text-sm text-muted-foreground">Đang tải dữ liệu...</div>}
            {error && <div className="p-4 text-sm text-red-500">{error}</div>}
            {/* Header */}
            <div className="mb-4 grid grid-cols-[250px_1fr] gap-4">
              <div className="font-sans text-sm font-semibold text-foreground">Task Name</div>
              <div className="grid grid-cols-10 gap-1">
                {weeks.map((week, index) => (
                  <div key={index} className="text-center text-xs font-medium text-muted-foreground">
                    {week}
                  </div>
                ))}
              </div>
            </div>

            {/* Tasks */}
            <div className="space-y-3">
              {tasks.map((task) => (
                <div key={task.id} className="grid grid-cols-[250px_1fr] gap-4">
                  {/* Task Info */}
                  <div className="flex flex-col justify-center gap-1">
                    <div className="font-sans text-sm font-medium text-foreground">{task.name}</div>
                    <div className="flex items-center gap-2">
                      <Badge
                        variant={
                          task.status === "completed"
                            ? "default"
                            : task.status === "in-progress"
                              ? "secondary"
                              : "outline"
                        }
                        className={cn(
                          "text-xs",
                          task.status === "completed" && "bg-primary text-primary-foreground",
                          task.status === "in-progress" && "bg-accent text-accent-foreground",
                          task.status === "pending" && "bg-muted text-muted-foreground",
                        )}
                      >
                        {task.status === "completed"
                          ? "Completed"
                          : task.status === "in-progress"
                            ? "In Progress"
                            : "Pending"}
                      </Badge>
                      <span className="text-xs text-muted-foreground">{task.assignee}</span>
                    </div>
                  </div>

                  {/* Timeline */}
                  <div className="relative grid grid-cols-10 gap-1">
                    {/* Background grid */}
                    {weeks.map((_, index) => (
                      <div key={index} className="h-12 rounded-sm border border-border/50 bg-muted/20" />
                    ))}

                    {/* Task bar */}
                    <div
                      className="absolute top-0 h-12 rounded-md"
                      style={{
                        left: `${(task.start / totalWeeks) * 100}%`,
                        width: `${(task.duration / totalWeeks) * 100}%`,
                      }}
                    >
                      <div className="relative h-full overflow-hidden rounded-md bg-primary/20 ring-1 ring-primary/30">
                        {/* Progress bar */}
                        <div
                          className={cn(
                            "h-full transition-all duration-300",
                            task.status === "completed" && "bg-primary",
                            task.status === "in-progress" && "bg-accent",
                            task.status === "pending" && "bg-muted-foreground/30",
                          )}
                          style={{ width: `${task.progress}%` }}
                        />
                        {/* Progress text */}
                        <div className="absolute inset-0 flex items-center justify-center">
                          <span className="text-xs font-semibold text-foreground mix-blend-difference">
                            {task.progress}%
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Legend */}
            <div className="mt-8 flex flex-wrap items-center gap-4 border-t border-border pt-4">
              <div className="text-sm font-medium text-foreground">Status:</div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-sm bg-primary" />
                <span className="text-xs text-muted-foreground">Completed</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-sm bg-accent" />
                <span className="text-xs text-muted-foreground">In Progress</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-sm bg-muted-foreground/30" />
                <span className="text-xs text-muted-foreground">Pending</span>
              </div>
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
